import CustomModal from "components/CustomModal";
import React from "react";
import { useState } from "react";
import ApplyJobform from "./ApplyJobform";
import { NA } from "shared/Constants";
import { dateWithWeekDay } from "shared/Utilities";
import { Pagination } from "components/Pagination";
import "./style.scss";
import moment from "moment";

function VacancyCard({
    data = [],
    onCardClick = () => {},
    onApply = () => {},
    jobSubmit = () => {},
    user_id = "",
}) {
    return (
        <>
            {data.length ? (
                <div className="row align-items-end">
                    {data?.map((i, index) => {
                        console.log(i?.applied_users, user_id, i?.applied_users?.includes(user_id));
                        return (
                            <div id={index} className="col-md-4 mb-3">
                                <div
                                    className="card"
                                    onClick={() => {
                                        onCardClick(i);
                                    }}
                                >
                                    <div className="card-title text-center">
                                        <h2 className="h5 text-capitalize">
                                            {i?.job_name ? i?.job_name : NA}
                                        </h2>
                                        <span className="text-small">
                                            {i?.created_at
                                                ? moment(i?.created_at).format("ddd, DD MMM YYYY")
                                                : NA}
                                        </span>
                                    </div>
                                    <div className="card-body p-4">
                                        <h6 className="h6 mb-0 sub_title text-center">{`${
                                            i?.branch_address?.branch_name
                                                ? i?.branch_address?.branch_name
                                                : ""
                                        }${
                                            i?.branch_address?.pharmacy_name
                                                ? " - " + i?.branch_address?.pharmacy_name
                                                : ""
                                        }${
                                            i?.branch_address?.postal_code
                                                ? " - " + i?.branch_address?.postal_code
                                                : ""
                                        }`}</h6>
                                        <div className="d-flex align-items-center justify-content-center">
                                            <label className="h5 my-3 font-bold">{`£${
                                                i?.start_rate ? i?.start_rate : 0
                                            }-${i?.end_rate ? i?.end_rate : 0}/hr`}</label>
                                            {/* <span className="h5 my-3">{`£${i?.start_rate ? i?.start_rate : 0}-${i?.end_rate ? i?.end_rate : 0
                                                }/hr`}</span> */}
                                        </div>
                                        <div className="text-center">
                                            <button
                                                className={`btn btn-md btn-outline-secondary ${
                                                    i?.applied_users?.includes(user_id)
                                                        ? "text-secondary disabled"
                                                        : ""
                                                }`}
                                                onClick={() => {
                                                    if (!i?.applied_users?.includes(user_id)) {
                                                        onApply(i);
                                                    }
                                                }}
                                            >
                                                {i?.applied_users?.includes(user_id)
                                                    ? `Applied`
                                                    : `Apply`}
                                            </button>
                                        </div>
                                    </div>
                                </div>
                            </div>
                        );
                    })}
                </div>
            ) : (
                <h6 className="no_result h6 text-center p-5">{"No Vacancy Availiable"}</h6>
            )}
        </>
    );
}

export default VacancyCard;
