import BreadCrumb from "components/BreadCrumb";
import JobView from "components/JobView";
import React, { useEffect, useState } from "react";
import { useHistory } from "react-router-dom";
import { ERROR_MESSAGES, ROUTES, SNACK_MESSAGES, SNACK_VARIANT, USER_TYPE } from "shared/Constants";
import ViewVacancy from "views/authenticated/Admin/VacanciesManagement/Vacancies/View";
import { VACANCY } from "views/authenticated/Admin/VacanciesManagement/Vacancies/constants";
import { STRINGS } from "./constants";
import VacancyCard from "components/VacancyCard";
import Footer from "components/Footer/Footer";
import JoinTeam from "./JoinTeam";
import CustomModal from "components/CustomModal";
import { useDispatch, useSelector } from "react-redux";
import useNotification from "hooks/Notifications";
import moment from "moment";
import { applyJob, viewJob } from "redux/Actions/LocumWebPanel";
import ApplyJobform from "components/VacancyCard/ApplyJobform";
import useGetIdFromPath from "hooks/Utilities/useGetIdFromPath";

function Vacancy_View() {
    const history = useHistory();
    const ratingData = [
        {
            title: VACANCY.VACANCY_VIEW.STAFF_LEVELS,
            rating: 2,
        },
        {
            title: VACANCY.VACANCY_VIEW.KNOWLEDGE,
            rating: 4,
        },
        {
            title: VACANCY.VACANCY_VIEW.SUPPORT,
            rating: 2,
        },
        {
            title: VACANCY.VACANCY_VIEW.ACCESSIBILITY,
            rating: 3,
        },
        {
            title: VACANCY.VACANCY_VIEW.PAYMENT,
            rating: 5,
        },
        {
            title: VACANCY.VACANCY_VIEW.OVERALL,
            rating: 4,
        },
    ];
    const [refresh, setRefresh] = useState(false);
    const dispatch = useDispatch();
    const profile = useSelector((state) => state.common.profileData) || {};
    const [msg, sendNotification] = useNotification();
    const [showModal, setShowModal] = useState(false);
    const [selectedJob, setSelectedJob] = useState({});
    const [notesValue, setNotesValue] = useState("");
    const [priceValue, setPriceValue] = useState("");
    const id = useGetIdFromPath();
    const [vacanyErrors, setVacancyErrors] = useState({
        rate: "",
    });

    useEffect(() => {
        setPriceValue("");
        setNotesValue("");
    }, [showModal]);

    const onChangeNote = (e) => {
        setNotesValue(e.target.value);
    };
    const onChangePrice = (e) => {
        const min = selectedJob?.start_rate;
        const max = selectedJob?.end_rate;
        const value = e.target.value.trim();
        let regex = new RegExp("^\\d{1,4}(\\.\\d{1,2})?$");
        handleChangeError("rate", value ? "" : ERROR_MESSAGES.GENERIC_ERROR);
        if (value?.length === 0 || value === "") {
            handleChangeError("rate", ``);
        } else if (value < min|| value > max ) {
            handleChangeError("rate", `Please enter a price between ${min} and ${max}.`);
        } else {
            // Set the price value in the state
            handleChangeError("rate", ``);
        }
        setPriceValue(value);
    };

    const applyForJob = (e) => {
        e.preventDefault();
        let payload = {
            job_id: selectedJob?.id,
            candidate_id: profile?.id,
            description: notesValue,
            rate: priceValue,
        };
        dispatch(
            applyJob(
                payload,
                (res) => {
                    ///onsucess
                    setShowModal(false);
                    setPriceValue("");
                    setNotesValue("");
                    setRefresh(prev => !prev)
                },
                (err) => {
                    setShowModal(false);
                    sendNotification({
                        msg: err || SNACK_MESSAGES.OOPS_SOMETHING_WENT_WRONG,
                        variant: SNACK_VARIANT.ERROR,
                    });
                    setPriceValue("");
                    setNotesValue("");
                },
            ),
        );
    };

    const onSucess = (data, onSucess = () => {}) => {
        setSelectedJob(data);
    };

    const onApply = () => {
        setShowModal(true);
    };

    const handleChangeError = (field, message) => {
        setVacancyErrors((prevState) => {
            let errorState = { ...prevState };
            errorState[field] = message;
            return errorState;
        });
    };

    const renderApplyJobModal = () => {
        return (
            <CustomModal
                isOpen={showModal}
                medium={true}
                handleToggle={() => setShowModal((prev) => !prev)}
            >
                <div className="text-start">
                    <h2 className="text-center h5 mb-4">{"Apply for Job"}</h2>
                    <div className="text-start">
                        <label className="text-primary mb-3"><strong>{`Original Rate £${
                            selectedJob?.start_rate ? selectedJob?.start_rate : 0
                        }-${selectedJob?.end_rate ? selectedJob?.end_rate : 0}/hr`}</strong></label>
                        <p className="text-primary"><strong>Please Note:</strong> This is not a guarantee that your request will be accepted. Your request either be accepted or rejected.</p>
                        <p className="text-primary"><strong>Proposed Rate:</strong> Please enter the rate you would like to suggest.</p>
                        <div className="text-left">
                            <ApplyJobform
                                data={selectedJob}
                                priceValue={priceValue}
                                notesValue={notesValue}
                                onChangeNote={onChangeNote}
                                onChangePrice={onChangePrice}
                                errorMessages={vacanyErrors.rate}
                                sendRequest={applyForJob}
                            />
                        </div>
                    </div>
                </div>
            </CustomModal>
        );
    };

    return (
        <div className="inner_pages ">
            <div className="container">
                <div className="d-flex flex-wrap align-items-center justify-content-between mt-5">
                    <BreadCrumb
                        onClick={() => history.push(ROUTES.VACANCIES)}
                        previousLink={VACANCY.VACANCIES}
                        currentLink={VACANCY.VIEW_JOBS}
                    />
                    <div className="btn_group">
                        <button
                            className={`btn btn-md btn-primary m-1 ${
                                selectedJob?.applied_users?.includes(profile?.id) ? "disabled" : ""
                            }`}
                            onClick={selectedJob?.applied_users?.includes(profile?.id) ? null : onApply}
                        >
                            {selectedJob?.applied_users?.includes(profile?.id)
                                ? "Applied"
                                : "Apply for Job"}
                        </button>
                    </div>
                </div>
                <div className="d-flex flex-column justify-content-center mt-2">
                    <JobView
                        ratingData={ratingData}
                        Data={[]}
                        isCandidateView={true}
                        type={USER_TYPE.LOCUM}
                        onSucess={onSucess}
                        refreshData={refresh}
                    />
                </div>
            </div>
            <JoinTeam />

            <Footer />
            {renderApplyJobModal()}
        </div>
    );
}

export default Vacancy_View;
